.page-form__header {
  position: relative;
  padding-top: 49px;
  background-color: @bg-header;

  &::after {
    .pseudo();
    width: 100%;
    height: 21px;
    bottom: -20px;
    left: 0;
    background-color: inherit;
    border-radius: 0 0 20px 20px;
  }
}

.page-form__wrapper-top {
  position: relative;
}

.page-form__title {
  position: absolute;
  bottom: -20px;
  left: 20px;
  .clear();
  padding: 0 5px 13px;
  color: #ffffff;
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  text-align: left;
  z-index: 1;

  &::after {
    .pseudo();
    height: 5px;
    left: 3px;
    right: 3px;
    bottom: -2.5px;
    background-color: @bg-sequence1;
    border-radius: 2px;
    z-index: 1;
  }
}

@media (min-width: @tablet-width) {
  .page-form__header {
    position: relative;
    padding-top: 84px;
    padding-bottom: 7px;
    background-color: @bg-header;

    &::after {
      .pseudo();
      width: 100%;
      height: 61px;
      bottom: -60px;
      left: 0;
      background-color: inherit;
      border-radius: 0 0 60px 60px;
    }
  }

  .page-form__wrapper-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  .page-form__title {
    position: relative;
    bottom: auto;
    left: auto;
    .clear();
    margin-left: 34px;
    font-size: 60px;
    line-height: 70px;

    &::after {
      height: 8px;
      left: 0;
      right: 0;
      bottom: -71px;
      border-radius: 4px;
    }
  }
}

@media (min-width: @desktop-width) {
  .page-form__header {
    padding-top: 92px;
    padding-bottom: 8px;
  }

  .page-form__title {
    .clear();
    margin-left: 67px;
  }
}
