.add-profile {
  position: relative;
  padding-top: 68px;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
  text-align: center;
  background-color: @bg-catalog;
  z-index: 5;

  &::after {
    .pseudo();
    width: 100%;
    height: 31px;
    left: 0;
    bottom: -30px;
    border-radius: 0 0 30px 30px;
    background-color: @bg-catalog;
    z-index: -1;
  }
}

.add-profile__wrapper {
  margin-bottom: 70px;
  text-align: left;
  color: @text;
}

.add-profile__title {
  .add-profile & {
    display: block;
    margin-bottom: 35px;
    padding: 15px 144px 13px 25px;
    text-decoration: none;
  }
}

.add-profile__list {
  position: relative;
  display: flex;
  flex-direction: column;
  .clear();
  margin-bottom: 42px;
  padding-left: 25px;

  &::before {
    .pseudo();
    .size(99);
    right: 0;
    top: -135px;
    background: url("../img/rates@1x.png") no-repeat;
    background-size: 99px 99px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background: url("../img/rates@2x.png") no-repeat;
      background-size: 99px 99px;
    }
  }
}

.add-profile__item {
  display: flex;
  justify-content: space-between;
  list-style: none;

  &:not(:last-child) {
    margin-bottom: 15px;
  }
}

.add-profile__item-wrap {
  display: flex;
  flex-direction: column;
  padding-top: 7px;
}

.add-profile__number {
  display: flex;
  justify-content: center;
  align-items: center;
  .size(34);
  font-size: 16px;
  font-weight: 400;
  line-height: 14px;
  border-radius: 50%;
  color: #ffffff;

  .add-profile__item:nth-child(1) & {
    background-color: @bg-sequence2;
  }

  .add-profile__item:nth-child(2) & {
    background-color: @bg-sequence3;
  }

  .add-profile__item:nth-child(n + 3) & {
    background-color: @bg-sequence4;
  }
}

.add-profile__business-link {
  width: 100%;
  margin-bottom: 8px;
  color: rgba(19, 37, 118, 1);
  text-decoration: none;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;

  &:hover,
  &:focus {
    color: rgba(19, 37, 118, 0.7);
    cursor: pointer;
  }

  &:active {
    color: rgba(19, 37, 118, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .add-profile {
    padding-top: 134px;
    font-size: 28px;
    font-weight: 300;
    line-height: 33px;

    &::after {
      height: 61px;
      bottom: -60px;
      border-radius: 0 0 60px 60px;
      z-index: 0;
    }
  }

  .add-profile__wrapper {
    margin-bottom: 70px;
  }

  .add-profile__title {
    .add-profile & {
      .clear();
      margin-bottom: 74px;
      text-align: center;
      letter-spacing: -0.1px;
    }
  }

  .add-profile__list {
    position: relative;
    margin-bottom: 30px;
    padding: 0;
    z-index: 1;

    &::before {
      width: 100px;
      top: auto;
      bottom: -115px;
      right: 44px;
      z-index: 3;
    }

    &::after {
      .pseudo();
      top: -42px;
      right: 0;
      bottom: -42px;
      left: 490px;
      background-color: @bg-country;
      border-radius: 20px;
      z-index: -1;
    }
  }

  .add-profile__item {
    flex-direction: row-reverse;
    justify-content: flex-end;
    list-style: none;

    &:not(:last-child) {
      margin-bottom: 0;
    }
  }

  .add-profile__item-wrap {
    flex-direction: row;
    justify-content: space-between;
    flex-grow: 1;
    padding-top: 16px;
  }

  .add-profile__description {
    flex-grow: 1;
  }

  .add-profile__number {
    .size(67);
    margin-right: 23px;
    padding-bottom: 2px;
    font-size: 28px;
    line-height: 28px;
  }

  .add-profile__price {
    margin-right: 34px;
  }

  .add-profile__business-link {
    .add-profile & {
      position: relative;
      margin-bottom: 5px;
      padding-left: 90px;
      padding-top: 25px;
      padding-bottom: 22px;
      font-size: 20px;
      line-height: 20px;
      text-align: left;

      &:hover::after,
      &:focus::after {
        opacity: 0.7;
      }

      &::after {
        .pseudo();
        .size(67);
        top: 0;
        left: 0;
        background: url("../img/business.svg") no-repeat;
        background-size: 17px 21px;
        background-position: center;
        border-radius: 50%;
        z-index: 10;
      }

      &::before {
        .pseudo();
        .size(67);
        top: 0;
        left: 0;
        background-color: @bg-country;
        border-radius: 50%;
      }

      &:active::after {
        opacity: 0.3;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .add-profile {
    padding-top: 164px;
    padding-bottom: 100px;
  }

  .add-profile__wrapper {
    margin-bottom: 70px;
    background-size: 198px;
    background-position: right center;
  }

  .add-profile__title {
    .add-profile & {
      margin-bottom: 132px;
      text-align: center;
    }
  }

  .add-profile__list {
    padding-left: 71px;
    padding-right: 310px;
    margin-bottom: 33px;

    &::before {
      .size(198);
      top: 0;
      right: 74px;
      bottom: 0;
      background-size: 198px;
    }

    &::after {
      top: -50px;
      right: 186px;
      bottom: -50px;
      left: 690px;
      background-color: @bg-country;
      border-radius: 20px;
      z-index: -1;
    }
  }

  .add-profile__item-wrap {
    padding-top: 17px;
  }

  .add-profile__number {
    padding-top: 2px;
    padding-bottom: 0;
  }

  .add-profile__business-link {
    .add-profile & {
      margin-left: 71px;
      width: 400px;
    }
  }
}
